import qs from "qs"
import { axiosClient } from "@rp/react/axios-client"
import { CONTENT_CATEGORIES_ROUTE } from "../content.constants"
import { ContentPackagesResponse } from "../content.types"

type GetContentCategoriesProps = {
  offset: number
  pageCount: number
}

async function getContentCategories({
  offset,
  pageCount,
}: GetContentCategoriesProps): Promise<ContentPackagesResponse> {
  const query = qs.stringify({ page: offset + 1, limit: pageCount })
  const { data } = await axiosClient.get<ContentPackagesResponse>(
    `${CONTENT_CATEGORIES_ROUTE}/?${query}`,
  )

  return data
}

export { getContentCategories }
